.left {
  width: 370px;
  border-right: 1px solid #aaa;
  min-height: 365px;
  padding: 5px;
  background-color: white;
  .left-wrapper {
    height: 100%;
    background: #fff;
    background-image: -webkit-gradient(linear, left top, left bottom,color-stop(0, rgba(0, 0, 0, .08)), color-stop(50%, rgba(255, 255, 255, .04)), color-stop(100%, rgba(0, 0, 0, .08)));
    min-height: 365px;
  }
  .base-form {
    padding: 50px 35px 25px !important;
    border-top-left-radius: 3px;
    text-align: left;
    label {
      font-size: 14px;
      font-weight: bold;
      margin-bottom: 10px; 
      text-shadow: 0 1px 0 #fff;
    }

    input {
      margin: 3px 0px;
      border: 1px solid #b0b0b0;
      width: 280px;
      font-size: 17px;
      padding: 5px;
      background-color: #fff;
      outline: none;
      vertical-align: middle;
      box-shadow: inset 0 1px 3px rgba(0, 0, 0, .15);
      border-radius: 3px;
    }

    button {
      background: -webkit-linear-gradient(top, #f9f30a 0%,#f9f100 50%,#f5ef06 50%,#efe900 100%);
      color: #343434;
      border: 1px solid rgba(0, 0, 0, .2);
      text-shadow: 0 1px 0 rgba(255, 255, 255, .5);
      font-weight: bold;
      padding: 6px 18px;
      margin-top: 10px;
      line-height: 17px;
      font-size: 14px;
      border-radius: 3px;
      box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, .2);
      &:hover {
        cursor: pointer;
      }
    }
  }

}

.secure {
    padding: 25px; 
    background: url(../media/noise.png);
    font-size: 11px;
    border-top: 1px solid #aaa;
    position: relative;
    border-right: 1px solid #aaa;;
    img {
      position: absolute;
      left: 14px;
    }
    a {
      margin-left: 32px;
      color: #252525;
      &:hover {
        color: #252525;
      }
    }
}

