#search {
  display: flex;
  justify-content: center;

  padding: 40px 0px;

  .container {
    width: 720px;
    display: flex;
    justify-content: space-between;
  }

  input {
    //margin: 3px 0px;
    border: 1px solid #b0b0b0;
    font-size: 18px;
    padding: 10px;
    background-color: #fff;
    outline: none;
    vertical-align: middle;
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, .15);
    border-radius: 3px;
    font-weight: bold;
    color: #626567;
    width: 536px;
  }

  button {
    background: -webkit-linear-gradient(top, #f9f30a 0%,#f9f100 50%,#f5ef06 50%,#efe900 100%);
    color: #343434;
    border: 1px solid rgba(0, 0, 0, .2);
    text-shadow: 0 1px 0 rgba(255, 255, 255, .5);
    font-weight: bold;
    text-align: center;
    padding: 10px;
    //margin-top: 10px;
    line-height: 17px;
    font-size: 18px;
    border-radius: 3px;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, .2);
    &:hover {
      cursor: pointer;
    }
    width: 180px;

  }

}