#header {
  height: 65px;
  background: -webkit-linear-gradient(top,  #f9f30a 0%,#f9f100 50%,#f5ef06 50%,#efe900 100%);
  color: #252525;
  border-bottom: 1px solid #f5f400;
  font-weight: bold;
  text-shadow: 0 1px 0 rgba(255, 255, 255, .75);
  box-shadow: 0 2px 8px rgba(0, 0, 0, .5);
  width: 100%;
  display: flex;
  justify-content: center;
  .container {
    width: 720px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .lang {
      border-radius: 3px;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 24px;
      width: 114px;
      font-size: 9px;
      border: 1px solid rgba(0, 0, 0, .2);
      background: -webkit-linear-gradient(top, #e5da02 0%,#eadb02 50%,#e2d702 50%,#dbcc00 100%);
      img {
        margin-right: 5px;
      }
      .en {
        display: flex;
      }
      .ro {
        display: flex;
        border-left: 1px solid rgba(0, 0, 0, 0.2);
        padding-left: 8px;
        margin-left: 8px;
      }
      img {
        width: 14px;
      }
    }
  }
}