#wrapper.container {
  width: 100%;
  display: flex;
  justify-content: center;
  //background: url(../media/noise.png);
  #masthead {
    width: 720px;
    display: flex;
    justify-content: flex-start;
    margin: 20px 0;
    h1 {
      font-size: 20px !important;
      text-shadow: 0 2px 2px rgba(0, 0, 0, .2);
      margin-top: 5px;
    }
  }
}

#content {
  width: 720px;
  background: rgba(255, 255, 255, .4);
  border: 1px solid #aaa;
  border-radius: 3px;
  box-shadow: 0 0 2px #00000033;
  display: flex;
  min-height: 50vh;

}

#not-found {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rating {
  display: flex;
  justify-content: flex-end;

  .star {
    height: 32px;
  }

  .numbers {
    height: 32px;
    display: flex;
    align-items: flex-end;
    margin-left: 8px;

    .rating-value {
      font-size: 32px;
      font-weight: bold;
      margin: 0;
      padding: 0;
      line-height: 32px;
    }

    .rating-10 {
      font-size: 18px;
    }

    .rating-from {
      margin-left: 10px;
      font-size: 22px;

      .num {
        text-decoration: underline;
      }
    }
  }

}

#bank-info {
  width: 100%;

  .head {
    background-color: #333;
    color: #fff;
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    //padding: 0px 10px;

    .name {
      margin-left: 16px;

    }

    .rating {
      margin-right: 16px;
    }
  }

  .about {
    text-align: left;
    padding: 10px;

    .row {
      display: flex;
      justify-content: space-between;
      margin: 10px 0px;

      .t_left {
        width: 50%;
        font-weight: bold;
      }
      .t_right {
        width: 50%;
      }
    }

    .summary {
      margin-top: 40px;
      font-style: italic;
    }
  }

}

/* custom stuff*/

#bank-info.ALPHA .head {
  background-color: #566573;
}
